.Contact {
    display: flex;
    flex: 3;
    justify-content: space-around;
    background-color:rgba(255,255,255,.5);
    padding: 3rem 0
}

.ContactHeader {
    font-size: 5rem;
    margin: 0;
    text-shadow: 3px 1px 8px #00000075;
}

.ContactSheet {
    text-align: left;
    padding: 1.5rem;
    width: 22rem;
}

@media (max-width: 880px) {
    .ContactHeader {
        font-size: 3rem;
    }
}

@media (max-width: 700px) {
    .ContactHeader {
        display: none
    }
}